html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: black;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: Arial, Helvetica, sans-serif;
  margin: 2 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}


input{
  width: 100%;
  font-size: 3rem;
}
label{
  font-size: 2.5rem;
}

.formulario{
  display: flex;
  justify-content: space-between;
  gap: 2rem;
}


@media(min-width:768px){
  .card-grid{
      display: grid;
      grid-template-columns: 1fr 1fr;
      column-gap: 2rem;
  }
}

@media(min-width:1024px){
  .card-grid{
      display: grid;
      grid-template-columns: repeat(3,1fr);
      column-gap: 2rem;
  }
}

ul{
  padding: 0;
}

.imagen img{
 width: 100%;
}

.animate__jackInTheBox{
  --animate-duration: 3s;
}